import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';
import { ArticoloPage } from '../articolo/articolo';
import { LoadingController  } from 'ionic-angular';

@Component({
  selector: 'page-magazzino',
  templateUrl: 'magazzino.html'
})
export class MagazzinoPage {
  public searchTerm: string = '';
  public items: any;
  public loading: any;

  constructor(public navCtrl: NavController, private enumeraService:EnumeraService, public loadingController:LoadingController, public params:NavParams) {
    this.getdata('');
  }

  getdata(searchTerm){
    this.showLoading(); 
    this.enumeraService.requestStr = 'prgname=getCatalogo&viewname=catalogo&param[flg_matricole]=1&param[flg_solo_giacenti]=1&param[codice]=' + this.searchTerm;
    this.enumeraService.doRequest();
    this.enumeraService.getDataSet().subscribe(
     result => {
       this.items = result;
      
     },
     err =>{
       console.error("Error : "+err);
     } ,
     () => {
       console.log('getData completed');
       //console.log(this.items);
         //this.loading.dismiss();
       this.hideLoading();
        // se trovato un solo salta al dettaglio articolo
       if (this.items != null){
          if (this.items.length === 1){
           this.vediArticolo(this.items[0])
         }
      }
     }
   );
 
 }
 
 vediArticolo(item){
   this.navCtrl.push(ArticoloPage, {item: item})
 } 
 
 private showLoading() {
     this.loading = this.loadingController.create({
       content: "Please wait..."
     });
     this.loading.present();
     setTimeout(() => {
       this.loading.dismiss();
       }, 20000);
   }
 
   private hideLoading(){
     this.loading.dismiss();
   } 
}



