import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';

@Component({
  templateUrl: 'articolo.html',
  providers: [EnumeraService]
})


export class ArticoloPage {
  public item; items_details: any = 0;  
  constructor(public navCtrl: NavController, public params:NavParams, private enumeraService:EnumeraService) {
    this.item = params.get('item');
    this.enumeraService.requestStr = 'prgname=getArticolo&param[codice]=' + this.item.CODART;
    this.enumeraService.doRequest();
    this.getdata();
    
  }

    getdata(){
      this.enumeraService.getDataSet().subscribe(
      result => {
        this.items_details = result;

    },
    err =>{
      console.error("Error : "+err);
    } ,
    () => {
      console.log('getData completed');
    }
  );

  
  }

  prenotaMatricola(item){
    this.navCtrl.push(ArticoloPage, {item: item})
  } 

}



